/* Projects page */

.proj-image {
  /* width: 300px; */
  border-radius: 20px;
}

/* Contact page */

.contact-btns span {
  max-width: 0;
  -webkit-transition: max-width 1s;
  transition: max-width 1s;
  display: inline-block;
  vertical-align: top;
  white-space: nowrap;
  overflow: hidden;
}

.contact-btns:hover span {
  max-width: 7rem;
  /* padding-left: 5px; */
}

.contact-btns:hover span::before {
  content: "|";
  padding-left: 5px;
  padding-right: 5px;
  color: #ffffff;
}

.particle-container {
  position: relative;
  background-color: #fff;
  z-index: -1;
  width: 100vw;
  /* height: 90vh; */
}

.color-nameBlue {
  color: #000a94 !important;
}

.iconCard {
  height: 100px;
  width: 90px;
}

.contactCard{
  /* width: 75%; */
  display: flex;
  justify-content: center;
  flex-flow: column wrap;
  border-radius: 6%;
  min-height: 180px;
}

.contactCard img{
  height: 70px;
}

.contactCard:hover{
  cursor: pointer;
  box-shadow: 1px 1px 10px grey !important;
}

